.odds-ends {
  display: flex;
  flex-direction: column;
  margin: 20px 0 0 0;
}

.odds-ends a {
  font-size: 30px;
  margin: 0 0 20px 0;
}

.odds-ends a:last-child {
  margin: 0;
}

.odds-ends__row {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 0 50px 0;

  .odds-ends__column:first-child {
    margin: 0 0 50px 0;
  }
}

.odds-ends__column {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.odds-ends__column__header {
  font-size: 28px;
  margin: 0 0 10px 0;
}

.odds-ends__column__desc {
  width: 60%;
  font-size: 14px;
  line-height: 18px;
  text-align: center;
  margin: 0 0 25px 0;
}

.odds-ends__column__link {
  font-size: 10px;
  margin: 0 0 15px 0;

  a {
    font-size: 10px;
  }
}

@media only screen and (min-width: 500px) {
  .odds-ends__row {
    flex-direction: row;
    justify-content: space-around;
    margin: 0;

    .odds-ends__column:first-child {
      margin: 0;
    }
  }
}
