$N: 12;
$measurements: 25;
$padding: 10;

.lights-out {
  height: #{($N * $measurements) + ($N * $padding) + $padding}px;
  width: #{($N * $measurements) + ($N * $padding) + $padding}px;
  position: relative;
  background: white;
  border: 2px solid lightgray;
  border-radius: 8px;
}

.lights-out__square {
  position: absolute;
}

@for $idx from 0 through ($N * $N) {
  .lights-out__square--#{$idx} {
    top: #{floor($idx / $N) * ($measurements + $padding) + $padding}px;
    left: #{($idx % $N) * ($measurements + $padding) + $padding}px;
  }
}

.lights-out input {
  appearance: none;
  -webkit-appearance: none;
  height: #{$measurements}px;
  width: #{$measurements}px;
  background: lightgray;
  border-radius: 0;
  margin: 0;
  cursor: pointer;

  &:checked {
    background: darkgray;
  }
}

@mixin checked_dudes($color) {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  height: #{$measurements}px;
  width: #{$measurements}px;
  background: $color;
  opacity: 0.6;
}

// ROWS
@mixin checked_loop_before($n) {
  @for $idx from 1 through ($N  - 1) {
    & ~ .lights-out__square--#{$n + $idx}:before {
      @include checked_dudes(blue);
    }
  }
}

@for $idx from 1 through ($N - 1) {
  .lights-out__square--#{$idx * $N}:checked {
    @include checked_loop_before($idx * $N);
  }
}

// COLUMNS
@mixin checked_loop_after($n) {
  @for $idx from 1 through ($N  - 1) {
    & ~ .lights-out__square--#{$idx * $N + $n}:after {
      @include checked_dudes(red);
    }
  }
}

@for $idx from 1 through ($N - 1) {
  .lights-out__square--#{$idx}:checked {
    @include checked_loop_after($idx);
  }
}
