.css-flower {
  position: relative;
  height: 400px;
  width: 400px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: #5FD17A;
  border-radius: 3px;
  z-index: 1;
}

@mixin petal-animation($n) {
  animation-name: fade, move_#{$n};
  animation-duration: 0.7s, 0.7s;
  animation-delay: 0s, 0s;
  animation-iteration-count: 1, 1;
}

@keyframes fade {
  0%   { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes move_1 { // 0
  0%   {  left: 0;        bottom: 10px;  }
  100% {  left: 0;        bottom: 60px;  }
}
@keyframes move_2 { // 30
  0%   {  left: 10px;     bottom: 7px;  }
  100% {  left: 35px;     bottom: 51px;  }
}
@keyframes move_3 { // 60
  0%   {  left: 17px;     bottom: 0px;  }
  100% {  left: 61px;     bottom: 25px;  }
}
@keyframes move_4 { // 90
  0%   {  left: 20px;     bottom: -10px;  }
  100% {  left: 70px;     bottom: -10px;  }
}
@keyframes move_5 { // 120
  0%   {  left: 17px;     bottom: -20px;  }
  100% {  left: 61px;     bottom: -45px;  }
}
@keyframes move_6 { // 150
  0%   {  left: 10px;     bottom: -27px;  }
  100% {  left: 35px;     bottom: -71px;  }
}
@keyframes move_7 { // 180
  0%   {  left: 0;        bottom: -30px;  }
  100% {  left: 0;        bottom: -80px;  }
}
@keyframes move_8 { // 210
  0%   {  left: -10px;    bottom: -27px;  }
  100% {  left: -35px;    bottom: -71px;  }
}
@keyframes move_9 { // 240
  0%   {  left: -17px;    bottom: -20px;  }
  100% {  left: -61px;    bottom: -45px;  }
}
@keyframes move_10 { // 270
  0%   {  left: -20px;    bottom: -10px;  }
  100% {  left: -70px;    bottom: -10px;  }
}
@keyframes move_11 { // 300
  0%   {  left: -17px;    bottom: 0;  }
  100% {  left: -61px;    bottom: 25px;  }
}
@keyframes move_12 { // 330
  0%   {  left: -10px;    bottom: 7px;  }
  100% {  left: -35px;    bottom: 51px;  }
}

$petal-color: rgba(112, 51, 173, 1);
$pistil-color: rgba(227, 187, 28, 1);

.css-flower__input {
  appearance: none;
  -webkit-appearance: none;
  height: 50px;
  width: 50px;
  background: #5FD17A;
  border-radius: 50%;
  box-shadow: inset 0 0 0 3px $petal-color;
  cursor: pointer;
  z-index: 3;
  transition:
    background 0.3s,
    box-shadow 0.3s;

  &:checked {
    background: $pistil-color;
    box-shadow: inset 0 0 0 18px $petal-color;
  }

  &:checked ~ .css-flower__petals .css-flower__petals__petal--01 {
    @include petal-animation(1);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--02 {
    @include petal-animation(2);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--03 {
    @include petal-animation(3);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--04 {
    @include petal-animation(4);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--05 {
    @include petal-animation(5);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--06 {
    @include petal-animation(6);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--07 {
    @include petal-animation(7);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--08 {
    @include petal-animation(8);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--09 {
    @include petal-animation(9);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--10 {
    @include petal-animation(10);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--11 {
    @include petal-animation(11);
  }
  &:checked ~ .css-flower__petals .css-flower__petals__petal--12 {
    @include petal-animation(12);
  }
}

.css-flower__petals {
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  z-index: 2;
}

.css-flower__petals__petal {
  height: 20px;
  width: 2px;
  position: absolute;
  background: $petal-color;
}

.css-flower__petals__petal--02 {
  transform: rotate(30deg);
}
.css-flower__petals__petal--03 {
  transform: rotate(60deg);
}
.css-flower__petals__petal--04 {
  transform: rotate(90deg);
}
.css-flower__petals__petal--05 {
  transform: rotate(120deg);
}
.css-flower__petals__petal--06 {
  transform: rotate(150deg);
}
.css-flower__petals__petal--07 {
  transform: rotate(180deg);
}
.css-flower__petals__petal--08 {
  transform: rotate(210deg);
}
.css-flower__petals__petal--09 {
  transform: rotate(240deg);
}
.css-flower__petals__petal--10 {
  transform: rotate(270deg);
}
.css-flower__petals__petal--11 {
  transform: rotate(300deg);
}
.css-flower__petals__petal--12 {
  transform: rotate(330deg);
}
